#page {
  background-image: url('https://storage.googleapis.com/website_media/MEDIA/other/backgroundtexturagrande.jpg');
  background-color: rgba(0, 0, 0, 0.8);
}

#footer a {
  color:white;
  text-decoration: none;
}
p{
 text-justify: auto;
}


#menu {
  position: -webkit-sticky;
  position: sticky;
  top: 3rem;
  z-index: 9;

}

#menuBox {
  position: -webkit-sticky;
  position: sticky;
  top: 1rem;
  z-index: 1;

}

#smoothLink {
  -webkit-transform: rotate(-90deg);
    -moz-transform: rotate(-90deg);
    -o-transform: rotate(-90deg);
    -ms-transform: rotate(-90deg);
    transform: rotate(-90deg);
}

#smoothLink:hover {
  text-decoration: none;
}

#subMenuCasulo, #subMenuOpaaco, #subMenuSelvIkei {
  border-left: 1px solid white;
}

#personas_projects::after {
  padding-left: 10px;
}
.container {
  display: flex;
  justify-content: space-around;
  align-items: flex-start;
}

AnchorLink {
  color: white;
}
span {
  display: block;
  color: white;
}
